import { prisma } from "../lib/db"; // Using relative path to be safe

export default async function Home() {
  // Fetch users from your Neon database
  const users = await prisma.user.findMany();

  return (
    <main className="min-h-screen bg-zinc-950 text-white p-12">
      <div className="max-w-2xl mx-auto">
        <h1 className="text-5xl font-black mb-8 bg-gradient-to-r from-cyan-400 to-blue-500 bg-clip-text text-transparent">
          Next.js + Tailwind 4
        </h1>

        <div className="grid gap-4">
          {users.length === 0 ? (
            <p className="text-zinc-500 italic">
              No users found. Open Prisma Studio to add one!
            </p>
          ) : (
            users.map((user) => (
              <div 
                key={user.id} 
                className="p-6 rounded-2xl bg-zinc-900 border border-zinc-800 hover:border-cyan-500/50 transition-all shadow-xl"
              >
                <p className="text-xl font-bold text-zinc-100">{user.name}</p>
                <p className="text-zinc-400">{user.email}</p>
              </div>
            ))
          )}
        </div>
      </div>
    </main>
  );
}